<template>
  <v-app id="inspire">
    <v-system-bar app>
      <v-spacer></v-spacer>
      <v-icon v-if="this.$vuetify.theme.dark"  @click="toggleTheme">mdi-brightness-4</v-icon>
      <v-icon v-if="!this.$vuetify.theme.dark" @click="toggleTheme">mdi-brightness-5</v-icon>
      <v-icon @click="toggleDrawer">mdi-arrow-left-right-bold</v-icon>
    </v-system-bar>

    <v-navigation-drawer
      v-model="drawer"
      app
    >
      <v-sheet
        color="deep-orange"
        class="pa-4"
      >
        <v-card-title class="lgo-text text-center justify-center font-weight-light"
        @click="goHome">Team <span class="font-weight-bold px-1"> Cloud </span> Suite</v-card-title>
      </v-sheet>
      <v-divider></v-divider>

      <v-list>
        <v-list-item  
          v-for="(link) in links" @click="doActionClick(link.action)"
          :key="link.icon"
        >
          <v-list-item-icon>
            <v-icon>{{ link.icon }}</v-icon>
          </v-list-item-icon>

          <v-list-item-content>
            <v-list-item-title>{{ link.title }}</v-list-item-title>
          </v-list-item-content>
        </v-list-item>
      </v-list>
    </v-navigation-drawer>

    <v-main>

      <component
      :is="activePage"
      :is-signed-in = "isSignedIn"
      @ev-signin="doActionClick( 'signin' )"
      ></component>

      <v-container
        v-if="isSignedIn"
        class="py-8 px-6"
        fluid
      >
        <v-row>
          <v-col cols="12">
            <v-card>
              <v-card-title class="text-center justify-left">Email Summary</v-card-title>
<template>
  <v-data-table
    dense
    :headers="aEmailSummaryHeaders"
    :items="aEmailSummary"
    :single-expand="tablSingleExpand"
    :expanded.sync="tablExpanded"
    item-key="MessageID"
    show-expand
    class="elevation-1 overflow-y-auto"
    max-height="calc(90vh - 520px)"
  >
<template v-slot:expanded-item="{ headers, item }">
            <td :colspan="headers.length">
              {{ item.MessageID }}
            </td>
      </template>
  </v-data-table>
  </template>
        </v-card>
          </v-col>
            <v-col cols="6">
              <v-card>
                <v-card-title class="text-center justify-left">Email Visualizations</v-card-title>
<v-tabs
  v-model="tabState"
  background-color="transparent"
  grow
>
  <v-tab
    v-for="tabItem in tabItems"
    :key="tabItem"
    @click="tabContent( tabItem )"
  >
    {{ tabItem }}
  </v-tab>
</v-tabs>

  <v-tabs-items v-model="tabState">
    <v-tab-item
      v-for="tabItem in tabItems"
      :key="tabItem"
    >
      <v-card flat>
        <v-card-text></v-card-text>
         <daily-breakdown-bar v-if="tabCurrent=='Daily Breakdown' || !tabCurrent" :chartdata="datacollection"></daily-breakdown-bar>
         <hourly-breakdown-heatmap v-if="tabCurrent=='Hourly Breakdown'" :chartdata="datacollection"></hourly-breakdown-heatmap>
      </v-card>
    </v-tab-item>
  </v-tabs-items>

           </v-card>
          </v-col>
        </v-row>
      </v-container>
    </v-main>
  </v-app>
</template>

<script>
import Vue from 'vue'
import VueGapi from 'vue-gapi'
import VueCharts from 'vue-chartjs'
import DailyBreakdownBar from './components/DailyBreakdownBar.vue'
import HourlyBreakdownHeatmap from './components/HourlyBreakdownHeatmap.vue'
import Signin from './components/views/Signin.vue'
import Wait from './components/views/Wait.vue'

  console.table( VueCharts )

  let doRevr = ( sIn = "tacocat" ) => ( sIn.split("").reverse().join("") );
  Vue.use(VueGapi, {
    apiKey: doRevr("wySB8UJSJTTR2d_k_uN_KWLdDtsYZutUDySazIA"),
    clientId: doRevr("moc.tnetnocresuelgoog.sppa.2n5jn3qn53b76h5sqc92ae7v02qjpep3-277572845182"),
    discoveryDocs: ['https://www.googleapis.com/discovery/v1/apis/gmail/v1/rest'],
    scope: 'https://www.googleapis.com/auth/gmail.readonly',
  })

  export default {
    components: {
      Signin,
      Wait,
      DailyBreakdownBar,
      HourlyBreakdownHeatmap
    },
    data: () => ({
      activePage: "Signin",
      brandColors: ["#BF8722","#F5771D","#DD4326","#F51D5C","#D91CEB"],
      isSignedIn: false, // (1) Track authenticated state
      drawer: true,
      tablExpanded: [],
      tablSingleExpand: false,
      //  Tabs
      tabState: null,
      tabItems: ["Daily Breakdown","Hourly Breakdown","Ave Response Time","Senders"],
      tabCurrent: null,
      // Graphs
      datacollection: {
        labels: ["Monday","Tuesday","Wednesday","Thursday", "Friday", "Saturday", "Sunday"],
        datasets: [
          {
            label: 'Sent',
            data: [15,22,26,16,7, 0, 0],
            borderColor: "#F5771D",
            backgroundColor: "#F5771D",
          },
          {
            label: 'Recieved',
            data: [18,20,22,24,9,1,2],
            borderColor: "#DD4326",
            backgroundColor: "#DD4326",
          },

        ]
      },
      // Links
      links: [
        {icon:'mdi-email-search', title:'Email Summary', action:'fetchinit'},
        {icon:'mdi-hand-pointing-up', title:'Premium Upgrade', action:'upgrade'},
        {icon:'mdi-logout', title:'Sign Out', action:'signout'}
      ],
      aEmailSummaryHeaders: [  //  See Payload > Headers
        {width: "200px", value:"From", text:"From", sortable:true},
        {width: "240px", value:"Date", text:"Date"},
        {width: "240px", value:"To", text:"To"},
        {width: "400px", value:"Subject", text:"Subject"}  //  {value:"DeliveredTo", text:"Recipient"}, 
        ],
      aEmailSummary: [] //  {"MessageID":"xx", "DeliveredTo":"qq", "Date":"qq", "From":"qq", "To":"qq", "Subject":"qq"}]
    }),
    watch: {
      isSignedIn: function(val) {
        if(val){ this.doActionClick("fetchinit") }
      }
    },
    methods: {
      tabContent( id ) {
        this.tabCurrent = id
      },
      goHome() {
      },
      signin() {
        this.$gapi.login().then(({ currentUser, hasGrantedScopes }) => {
          console.log("currentUser, hasGrantedScopes" )
          console.log({ currentUser, hasGrantedScopes })
        })
      },
      signout() {
        this.$gapi.logout()
      },
      toggleDrawer(){
        this.drawer = !this.drawer
      },
      toggleTheme(){
        this.$vuetify.theme.dark = !this.$vuetify.theme.dark
      },
      fetchGmailMessages(){
        let self = this
        let goglClient = this.$gapi.clientProvider.client;

        var user = goglClient.authInstance.currentUser.get();
        var oauthToken = user.getAuthResponse().access_token;
        console.log(oauthToken);
        var xhr = new XMLHttpRequest();
        var userId='me';
        xhr.open('GET',
            'https://gmail.googleapis.com/gmail/v1/users/'+userId+'/messages');
        xhr.setRequestHeader('Authorization',
            'Bearer ' + oauthToken);
        xhr.send();
        xhr.onload=function () {
            var data=JSON.parse(this.response);
            for (let i in data.messages)
            {
              self.fetchMailDetails(data.messages[i].id,userId,oauthToken);
            }
        }
      },
      fetchMailDetails(id,userId,oauthToken){
        let self = this
        let xhr1 = new XMLHttpRequest();
        xhr1.open('GET','https://gmail.googleapis.com/gmail/v1/users/'+userId+'/messages/'+id,true);
        xhr1.setRequestHeader('Authorization', 'Bearer ' + oauthToken);
        xhr1.send();
        xhr1.onload=function(){
          let emailDetails=JSON.parse(this.response)
          let emailDetHeaders = emailDetails.payload.headers
          self.aEmailSummary.push({
            "MessageID":self.extractVal(emailDetHeaders, "Message-ID"),
            "DeliveredTo":self.extractVal(emailDetHeaders, "Delivered-To"),
            "Date":self.extractVal(emailDetHeaders, "Date"),
            "From":self.extractVal(emailDetHeaders, "From"),
            "To":self.extractVal(emailDetHeaders, "To"),
            "Subject":self.extractVal(emailDetHeaders, "Subject")
          })
        }
      },
      extractVal(aHead, sName){
        let sVal = aHead.filter((v)=> (v.name===sName))
        return (sVal[0] && sVal[0].value) ? sVal[0].value : ""
      },
      doActionClick( action ){
        switch(action) {
          case "signin":
            this.signin()
            this.activePage = "Wait"
            break;
          case "fetchinit":
            this.fetchGmailMessages()
            break;
          case "signout":
            this.signout()
            this.activePage="Signin"
            break;
          case "upgrade":
            alert("Bad programmer, no donuts!")
            break;
        }
      }
    },
    created() {
      //  (2) Subscribe to authentication status changes
      this.$gapi.listenUserSignIn((isSignedIn) => {
        this.isSignedIn = isSignedIn
      })
    },
  }
</script>

<style scoped>
  .lgo-text {
    color: #fff;
    cursor: pointer;
    user-select: none;
  }
</style>
